.App {
    text-align: center;
}

.cell {
    width: 7vmin;
    height: 7vmin;
    border: 1px solid black;
}

input {
    font-size: 190%;
    text-align: center;
}

body {
    margin: 20px;
}

#submitButton {
    margin-top: 20px;
    height: 30px;
    width: 75px;
    font-size: 20px;
}

.row {
    margin: 0px;
}

@media screen and (max-width: 480px) {
    .cell {
        width: 1.3em;
        height: 1.3em;
        border: 0.5px solid black;
    }

    input {
        font-size: 100%;
        text-align: center;
    }

    body {
        margin: 20px;
    }

    #submitButton {
        margin-top: 20px;
        height: 30px;
        width: 75px;
        font-size: 20px;
    }
}